#view-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100vw;
    height: 100vh;
    overflow: hidden;

    z-index: 1;

    margin: 0;
    padding: 0;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

    background: transparent;
}

#sections-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    

    margin: 0;
    padding: 0;

    background: var(--section-container-bg);
    background: transparent;
}

@-webkit-keyframes top-enter {
    from {
        margin-top: 500%;
    }

    to {
        margin-top: 0px;
    }
}

@keyframes top-enter {
    from {
        margin-top: 500%;
    }

    to {
        margin-top: 0px;
    }
}

@-webkit-keyframes bottom-enter {
    from {
        margin-bottom: 500%;
    }

    to {
        margin-bottom: 0px;
    }
}

@keyframes bottom-enter {
    from {
        margin-bottom: 500%;
    }

    to {
        margin-bottom: 0px;
    }
}


.page-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;


    display: -ms-flexbox;


    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    color: white;
}

.page-container.bottom {
    -webkit-animation-name: bottom-enter;
            animation-name: bottom-enter;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
}

.page-container.top {
    -webkit-animation-name: top-enter;
            animation-name: top-enter;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
}

#home-container {
    position: relative;
    max-width: 100%;
    max-height: 100%;

    border-radius: 2rem;

    /*background: rgba(0, 0, 0, 0.25);*/
}

#home-container img {
    display: none;
    position: absolute;
    width: 100%;
    height: 100%;
}

@media only screen and (min-width: 960px) {
    #view-container {    
        display: -ms-flexbox;    
        display: flex;
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
        -ms-flex-pack: start;
            justify-content: flex-start;
        -ms-flex-align: center;
            align-items: center;
    }

    
    #sections-container {
        width: 100%;
        height: 100%;
    }

    .page-container {
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        width: 100%;
        height: 100%;
    }
}





